<script setup>
import {onActivated, ref} from "vue";
import api from "@/api";
import BreadCrumbs from "@/components/BreadCrumbs.vue";
import CardPlace from "@/components/CardPlace.vue";

const places = ref([])

api.place.getAll({ region: 'krasnodarskiy-kray', city: 'krasnodar' })
    .then(data => {
        places.value = data.places
    })
    .catch(e => {
        console.log(e)
    })

const breadcrumbs = [
    { name: 'Главная', path: '/' }
]

onActivated(() => {
    document.title = 'Парки и скверы в Краснодаре'
})

</script>

<template>
    <div class="places page-wrapper">
        <div class="container">
            <bread-crumbs :links="breadcrumbs" current="Парки и скверы в Краснодаре"/>
            <h1 class="page-title page-title_h1">
                Парки и скверы в Краснодаре
            </h1>
            <div class="places__grid">
                <template v-for="place in places" :key="place.id">
                    <card-place :data="place"/>
                </template>
            </div>
        </div>
    </div>
</template>

